.footer {
  display: flex;
  flex-direction: column;

  margin: 0 32px;

  .info-container {
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    margin-bottom: 32px;

    .info-field {
      h3 {
        font-size: 18px;
        margin-top: 32px;
        margin-bottom: 0;
      }

      p {
        font-size: 16px;
        margin-top: 8px;
      }

      .social-icon {
        cursor: pointer;
        width: 24px;
        height: 24px;
        object-fit: fill;
      }
    }
  }
  hr.solid {
    width: 100%;
    border-top: 1px solid #bbbbbb;
  }

  .info-container-mobile {
    justify-content: space-between;
    margin-bottom: 32px;

    .info-field {
      display: flex;
      align-items: end;
      h3 {
        font-size: 18px;
        margin-top: 24px;
        margin-bottom: 0;
      }

      p {
        font-size: 16px;
        margin-top: 6px;
      }

      .social-icon {
        cursor: pointer;
        width: 24px;
        height: 24px;
        object-fit: fill;
      }
    }
  }
}

.footer-mobile {
  display: flex;
  flex-direction: column;

  hr.solid {
    width: 100%;
    border-top: 1px solid #bbbbbb;
  }

  .info-container-mobile {
    justify-content: space-between;
    margin-bottom: 32px;

    .info-field {
      display: flex;
      align-items: end;
      h3 {
        font-size: 18px;
        margin-top: 24px;
        margin-bottom: 0;
      }

      p {
        font-size: 16px;
        margin-top: 6px;
      }

      .social-icon {
        cursor: pointer;
        width: 24px;
        height: 24px;
        object-fit: fill;
      }
    }
  }
}
