@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.home-container {
  animation: fadeIn 1s ease-in-out;

  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
  width: 100%;
  padding: 100px;

  .avatar-container {
    flex: 1;
    display: flex;
    justify-content: end;

    .avatar {
      height: 480px;
      object-fit: scale-down;
      margin-right: 16px;
    }
  }

  .info-container {
    flex: 1;
    margin: 32px;

    h1 {
      font-size: 100px;
      margin: auto;
    }

    h2 {
      font-size: 22px;
    }

    .about-me {
      font-size: 18px;
      margin-bottom: 32px;
    }

    .buttons-container {
      display: flex;

      button {
        margin-right: 8px;
      }
    }
  }
}

.home-container-mobile {
  animation: fadeIn 1s ease-in-out;
  width: 100vw;
  margin-top: 120px;

  .avatar-container-mobile {
    width: 100vw;

    .avatar-mobile {
      height: 35vh;
      width: 90vw;
      object-fit: scale-down;
    }
  }

  .info-container {
    flex: 1;

    h1 {
      font-size: 32px;
    }

    h2 {
      font-size: 24px;
    }

    .about-me {
      font-size: 16px;
      margin-bottom: 32px;
    }
  }
}
